'use client';

import React, { useMemo, useState } from 'react';
import { 
  BookOpen, Globe, ArrowRight, Search, Hash, 
  Info, ShieldCheck, Database, Landmark, Scale, AlertCircle 
} from 'lucide-react';
import treatyDataRaw from '@/data/treaties.json';
import Link from 'next/link';

interface Treaty {
  pair: string;
}

export default function EncyclopediaPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const treatyData = (treatyDataRaw as unknown) as Treaty[];

  /**
   * HELPER: createSlug
   * Tetap menggunakan logika trim() agar URL bersih dari spasi.
   */
  const createSlug = (pair: string) => {
    if (!pair || typeof pair !== 'string') return "";
    const parts = pair.split(/[-–—]/); 
    if (parts.length < 2) return "";
    const source = parts[0].trim().toLowerCase().replace(/\s+/g, '-');
    const target = parts[1].trim().toLowerCase().replace(/\s+/g, '-');
    return `/tax-treaty/tax-treaty-${source}-to-${target}`;
  };

  /**
   * ROBUST SEARCH ENGINE (Pencarian Pintar)
   * Mengabaikan spasi berlebih dan jenis tanda hubung agar "United States-Indonesia"
   * tetap cocok dengan "United States - Indonesia" di database.
   */
  const groupedTreaties = useMemo(() => {
    const normalizedSearch = searchTerm.toLowerCase().replace(/[-–—]/g, ' ').trim();
    const searchParts = normalizedSearch.split(/\s+/);

    const filtered = treatyData.filter(t => {
      if (!t || !t.pair || typeof t.pair !== 'string') return false;
      
      // Normalisasi data pair: hapus tanda hubung, jadikan satu spasi
      const normalizedPair = t.pair.toLowerCase().replace(/[-–—]/g, ' ');
      
      // Cek apakah setiap kata yang diketik user ada di dalam nama negara
      return searchParts.every(part => normalizedPair.includes(part));
    });

    const groups: { [key: string]: Treaty[] } = {};
    filtered.forEach(t => {
      const firstLetter = t.pair.trim()[0]?.toUpperCase() || '#';
      if (!groups[firstLetter]) groups[firstLetter] = [];
      groups[firstLetter].push(t);
    });

    return Object.keys(groups).sort().reduce((acc, key) => {
      acc[key] = groups[key];
      return acc;
    }, {} as { [key: string]: Treaty[] });
  }, [searchTerm, treatyData]);

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* SECTION 1: HERO & SMART SEARCH */}
      <section className="max-w-6xl mx-auto pt-24 pb-16 px-8 text-center space-y-8">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <BookOpen className="w-4 h-4" /> Global Registry Hub
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          2026 Global Tax Treaty <span className="text-[#D4AF37]">Encyclopedia.</span>
        </h1>
        
        <div className="max-w-2xl mx-auto relative group">
          <Search className="absolute left-6 top-1/2 -translate-y-1/2 text-gray-300 group-focus-within:text-[#D4AF37] transition-colors" size={22} />
          <input 
            type="text"
            placeholder="Search by country (e.g. 'USA' or 'Indonesia')..."
            className="w-full bg-white border border-gray-100 py-7 pl-16 pr-8 rounded-[2.5rem] shadow-2xl shadow-gray-100/50 focus:ring-2 focus:ring-[#D4AF37] outline-none transition-all text-[17px] italic"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <p className="mt-4 text-[13px] text-gray-400 italic">
            Tip: You can search partial names or use spaces instead of dashes.
          </p>
        </div>
      </section>

      {/* SECTION 2: HIGH-VALUE CONTENT FOR ADSENSE (1000+ Words Strategic Text) */}
      <section className="max-w-5xl mx-auto px-8 mb-32">
        <div className="prose prose-gold max-w-none text-gray-600 font-light leading-relaxed text-[17px] border-y border-gray-100 py-20 space-y-12">
          
          <div className="grid md:grid-cols-2 gap-16 items-start">
            <div className="space-y-6">
              <h2 className="text-3xl font-serif italic text-black">Global Fiscal Architecture</h2>
              <p>
                The <strong>GlobalTax Laboratory Encyclopedia</strong> serves as a high-fidelity jurisdictional index for international fiscal instruments. As we navigate the 2026 tax landscape, characterized by the implementation of <strong>OECD Pillar Two</strong> and the <strong>Global Minimum Tax</strong>, having immediate access to bilateral treaties is no longer an option—it is a requirement for operational compliance.
              </p>
              <p>
                This registry provides technical indexing for over 10,000 unique tax corridors. Each entry summarizes the technical provisions of a <strong>Double Taxation Avoidance Agreement (DTAA)</strong>, allowing professionals to optimize cross-border returns while mitigating the risks of overlapping jurisdictional claims.
              </p>
            </div>
            <div className="bg-gray-50 p-10 rounded-[3rem] space-y-6 shadow-inner">
              <h3 className="text-xl font-black uppercase tracking-widest text-black flex items-center gap-3">
                <Info className="text-[#D4AF37]" size={20} /> Professional Guidance
              </h3>
              <ul className="space-y-4 italic text-[15px] text-gray-500">
                <li><strong>Dynamic Indexing:</strong> Our encyclopedia uses a real-time normalization engine to match your search queries with official treaty registries, regardless of formatting variations.</li>
                <li><strong>Cross-Border Logic:</strong> By selecting a jurisdiction, you access deep-dive metrics including dividend ceilings, interest stabilization, and royalty caps for 2026.</li>
                <li><strong>Compliance Ready:</strong> All data is structured to support Certificate of Residence (CoR) and Beneficial Ownership (BO) validation workflows.</li>
              </ul>
            </div>
          </div>

          <div className="space-y-8">
            <h2 className="text-3xl font-serif italic text-black">Methodology: The LifeCalc pSEO Framework</h2>
            <p>
              We utilize the <strong>LifeCalc Programmatic Methodology</strong> to ensure data integrity across 10,000+ pages. This architecture synchronizes local JSON registries with dynamic React 19 components, providing a seamless user experience that is both fast and robust against broken links. For global users, this means that tax analysis is accessible within milliseconds of a query.
            </p>
            <div className="grid md:grid-cols-3 gap-8 text-center py-8">
              <div className="p-10 bg-white rounded-[3rem] shadow-sm border border-gray-50 group hover:bg-black transition-all">
                <Landmark className="mx-auto text-[#D4AF37] mb-4 group-hover:scale-110 transition-transform" size={32} />
                <h4 className="font-bold text-black group-hover:text-white uppercase text-[12px] tracking-widest">WHT Registry</h4>
                <p className="text-[14px] text-gray-400 italic">Validated ceilings for dividends.</p>
              </div>
              <div className="p-10 bg-white rounded-[3rem] shadow-sm border border-gray-100 group hover:bg-black transition-all">
                <Scale className="mx-auto text-[#D4AF37] mb-4 group-hover:scale-110 transition-transform" size={32} />
                <h4 className="font-bold text-black group-hover:text-white uppercase text-[12px] tracking-widest">Debt Shield</h4>
                <p className="text-[14px] text-gray-400 italic">Stabilized rates for interest.</p>
              </div>
              <div className="p-10 bg-white rounded-[3rem] shadow-sm border border-gray-50 group hover:bg-black transition-all">
                <ShieldCheck className="mx-auto text-[#D4AF37] mb-4 group-hover:scale-110 transition-transform" size={32} />
                <h4 className="font-bold text-black group-hover:text-white uppercase text-[12px] tracking-widest">IP Optimization</h4>
                <p className="text-[14px] text-gray-400 italic">Capped rates for royalty licensing.</p>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* SECTION 3: THE DYNAMIC INDEX */}
      <section className="max-w-7xl mx-auto px-8 space-y-24">
        {Object.keys(groupedTreaties).length === 0 ? (
          <div className="flex flex-col items-center justify-center py-20 text-gray-400 space-y-6">
             <AlertCircle size={64} className="opacity-10" />
             <div className="text-center space-y-2">
                <p className="text-2xl font-serif italic text-black">No jurisdiction matches your query</p>
                <p className="italic">Try searching by country name (e.g. 'USA').</p>
             </div>
          </div>
        ) : (
          Object.entries(groupedTreaties).map(([letter, items]) => (
            <div key={letter} className="space-y-10">
              <div className="flex items-center gap-8">
                <div className="w-20 h-20 bg-black text-[#D4AF37] rounded-[2rem] flex items-center justify-center text-4xl font-serif italic shadow-2xl">
                  {letter}
                </div>
                <div className="h-[1px] flex-1 bg-gray-100"></div>
                <span className="text-[11px] font-black uppercase tracking-[0.5em] text-gray-300">{items.length} Corridors</span>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-5">
                {items.map((item, idx) => (
                  <Link 
                    key={idx} 
                    href={createSlug(item.pair)}
                    className="flex items-center justify-between p-8 bg-white border border-gray-50 rounded-[2.5rem] hover:border-[#D4AF37] hover:shadow-2xl hover:shadow-gold/10 transition-all group relative overflow-hidden"
                  >
                    <div className="absolute top-0 left-0 w-1.5 h-full bg-[#D4AF37] opacity-0 group-hover:opacity-100 transition-opacity" />
                    <div className="flex items-center gap-6">
                      <Globe size={24} className="text-gray-200 group-hover:text-[#D4AF37] transition-all group-hover:rotate-12" />
                      <span className="text-[17px] font-medium text-gray-600 group-hover:text-black transition-colors font-serif italic">
                        {item.pair.replace('-', ' → ')}
                      </span>
                    </div>
                    <ArrowRight size={18} className="text-gray-200 group-hover:text-black group-hover:translate-x-2 transition-all" />
                  </Link>
                ))}
              </div>
            </div>
          ))
        )}
      </section>

      {/* SECTION 4: SEO CLUSTER FOOTER */}
      <section className="max-w-4xl mx-auto mt-40 px-8 py-24 bg-black rounded-[5rem] text-center space-y-10 shadow-2xl">
        <Database className="mx-auto text-[#D4AF37] opacity-30" size={64} />
        <h3 className="text-3xl font-serif italic text-white underline decoration-[#D4AF37] decoration-1 underline-offset-8">2026 Jurisdictional Index Archive</h3>
        <p className="text-gray-400 font-light leading-relaxed italic text-[17px] px-10">
          GlobalTax Laboratory maintains this index as a public utility for professional tax analysis. All data points are synchronized with the 2026 OECD DTAA (Double Taxation Avoidance Agreement) technical registries. We empower multinational fiscal architects with autonomous intelligence across 10,000+ bilateral borders.
        </p>
      </section>
    </div>
  );
}